import Vue from 'vue'
import Vuex from 'vuex'
import VuexPersistence from 'vuex-persist'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    num: -1,
    isLogin: false,
    
  },
  mutations: {
    SET_STATUS (state, options) {
      state.num = options.num;
      state.isLogin = options.isLogin;
    },
  },

  plugins: [
    new VuexPersistence({
      reducer: state => ({
        num: state.num,
        isLogin: state.isLogin,
      })
    }).plugin
  ] 
})
